<template>
    <div class="seller" ref="seller">
        <div class="seller-content">
            <div class="overview">
                <h1 class="title">{{seller.name}}</h1>
                <div class="desc">
                    <star class="star" :size="36" :score="seller.score"></star>
                    <span class="text">({{seller.ratingCount}})</span>
                    <span class="text">月售{{seller.sellCount}}单</span>
                </div>
                <ul class="remark">
                    <li class="block">
                        <h2>起送价</h2>
                        <div class="content">
                            <span class="stress">{{seller.minPrice}}</span>元
                        </div>
                    </li>
                    <li class="block">
                        <h2>商家配送</h2>
                        <div class="content">
                            <span class="stress">{{seller.deliveryPrice}}</span>元
                        </div>
                    </li>
                    <li class="block">
                        <h2>平均配送时间</h2>
                        <div class="content">
                            <span class="stress">{{seller.deliveryTime}}</span>分钟
                        </div>
                    </li>
                    
                </ul>
                <div class="favorite" @click="toggleFavorite">
                    <span class="iconfont icon-shoucang icon" :class="{'active':favorite}"></span>
                    <span class="text">{{favoriteText}}</span>
                </div>

            </div>
            <split></split>

            <div class="bulletin">

                <h1 class="title">公告与活动</h1>

                <div class="content-wrapper">
                    <p class="content">{{seller.bulletin}}</p>
                </div>

                <ul v-if="seller.supports" class="supports">
                    <li class="support-item" v-for="(item,key) in seller.supports" :key="key">
                        <span class="icon" :class="classMap[seller.supports[key].type]"></span>
                        <span class="text">{{seller.supports[key].description}}</span>
                    </li>
                </ul>

            </div>

            <split></split>

            <div class="pics">
                <h1 class="title">商家实景</h1>
                <div class="pic-wrapper" ref="picwrapper">
                    <ul class="pic-list" ref="piclist">
                        <li class="pic-item" v-for="(pic,index) in seller.pics" :key="index">
                            <img :src="pic" alt="">
                        </li>
                    </ul>
                </div>
            </div>

            <split></split>

            <div class="info">
                <h1 class="title">商家信息</h1>
                <ul>
                    <li class="info-item" v-for="(info,index) in seller.infos" :key="index">
                        {{info}}
                    </li>
                </ul>
            </div>

        </div>
    </div>
</template>

<script>
    import BScroll from 'better-scroll';
    import star from "../Star/Star.vue";
    import split from "../split/split.vue";

    export default {
        props: {
            seller: {
                type: Object
            }
        },
        components: {
            star,
            split
        },
        data() {
            return {
                favorite: false
            }
        },
        computed: {
            favoriteText() {
                return this.favorite ? '已收藏' : '未收藏';
            }
        },
        created() {
             this.classMap = ['decrease','discount','guarantee','invoice','special'] 
        },
        watch: {
            'seller'() {
                this.$nextTick(() => {
                this._initScroll();
                this._initPics();
                });
            }
        },
        mounted() {
            this.$nextTick(()=> {
                this._initScroll();
                this._initPics();
            })
        },
        methods: {

            _initScroll() {
                if (!this.scroll) {
                    this.scroll = new BScroll(this.$refs.seller, {
                        click: true
                    });
                } else {
                    this.scroll.refresh();
                }
            },

            _initPics() { 
                if (this.seller.pics) {
                    let picwidth = 120;
                    let margin = 6;
                    let width = (picwidth+margin) * this.seller.pics.length - margin;
                    this.$refs.piclist.style.width = width + "px";
                    this.$nextTick(() => {
                        if (!this.picScroll) {
                            this.picScroll = new BScroll(this.$refs.picwrapper, {
                                scrollX: true,
                                eventPassthrough: 'vertical'
                            });
                        } else {
                            this.picScroll.refresh();
                        }
                    })
                }
            },

            toggleFavorite(event) {
                if(event._construted) {
                    return;
                }
                this.favorite = !this.favorite;
            }

        }
    }

</script>

<style scoped lang="scss">

    @import "../../scss/mixin.scss";

    .seller {
        position: absolute;
        top: 4.65rem;
        left: 0;
        bottom: 0;
        width: 100%;
        overflow: hidden;
        .overview {
            position: relative;
            padding: 18px;
            .title {
                margin-bottom: 0.21rem;
                line-height: 0.37rem;
                color: rgb(7, 17, 27);
                font-size: 14px;
            }
            .desc {
                padding-bottom: 0.48rem;
                border-bottom: 1px solid rgba(7, 17, 27, 0.1);
                font-size: 0;
                .star {
                    display: inline-block;
                    margin-right: 0.21rem;
                    vertical-align: top;
                }
                .text {
                    display: inline-block;
                    margin-right: 0.32rem;
                    line-height: 0.48rem;
                    vertical-align: top;
                    font-size: 10px;
                    color: rgb(77, 85, 93);
                }
            }
            .remark {
                display: flex;
                padding-top: 0.48rem;
                .block {
                    flex: 1;
                    text-align: center;
                    border-right: 1px solid rgba(7, 17, 27, 0.1);
                    &:last-child {
                        border: none;
                    }
                    h2 {
                        margin-bottom: 0.1rem;
                        line-height: 0.26rem;
                        font-size: 10px;
                        color: rgb(147, 153, 159);
                    }
                    .content {
                        line-height: 0.64rem;
                        font-size: 10px;
                        color: rgb(7, 17, 27);
                        .stress {
                            font-size: 24px
                        }
                    }
                }
            }
            .favorite {
                position: absolute; 
                width: 1.33rem;
                right: 0.3rem;
                top: 0.48rem;
                text-align: center;
                .icon {
                    display: block;
                    margin-bottom: 0.1rem;
                    line-height: 0.64rem;
                    font-size: 24px;
                    color: #d4d6d9;
                    &.active {
                        color: rgb(240, 20, 20);
                    }
                }
                .text {
                    line-height: 0.26rem;
                    font-size: 10px;
                    color: rgb(77, 85, 93);
                }
            }
        }
        .bulletin {
            padding: 0.48rem 0.48rem 0 0.48rem;
            .title {
                margin-bottom: 0.21rem;
                line-height: 0.37rem;
                color: rgb(7, 17, 27);
                font-size: 14px;
            }
            .content-wrapper {
                padding: 0 0.32rem 0.42rem 0.32rem;
                border-bottom: 1px solid rgba(7,17,27,0.1);
                .content {
                    line-height: 0.64rem;
                    font-size: 12px;
                    color: rgb(240,20,20);
                }
            }
            .supports {
                .support-item {
                    padding: 0.42rem 0.32rem;
                    .icon {
                        display: inline-block;
                        vertical-align: top;
                        width: 0.32rem;
                        height: 0.32rem;
                        margin-right: 0.27rem;
                        background-size: 0.32rem;
                        background-repeat: no-repeat;
                        &.decrease {
                            @include bg-image('decrease_4');
                        }
                        &.discount {
                            @include bg-image('discount_4');
                        }
                        &.guarantee {
                            @include bg-image('guarantee_4');
                        }
                        &.invoice {
                            @include bg-image('invoice_4');
                        }
                        &.special {
                            @include bg-image('special_4');
                        }
                    }
                    .text {
                        line-height: 16px;  
                        font-size: 12px;
                        color: rgb(7, 17, 27);
                    }
                }
            }
        }
        .pics {
            padding: 18px;
            .title {
                margin-bottom: 12px;
                line-height: 14px;
                color: rgb(7, 17, 27);
                font-size: 14px;
            }
            .pic-wrapper {
                width: 100%;
                overflow: hidden;
                white-space: nowrap;
                .pic-list {
                    font-size: 0;
                    .pic-item {
                        display: inline-block;
                        margin-right: 6px;
                        width: 120px;
                        height: 90px;
                        &:last-child {
                            margin: 0
                        }
                        img {
                            width: 100%;
                            height: 100%;
                        }
                    }
                }
            }
            
        }
        .info {
            padding: 0.48rem 0.48rem 0 0.48rem;
            color: rgb(7, 17, 27);
            .title {
                padding-bottom: 0.32rem;
                line-height: 0.37rem;
                border-bottom: 1px solid rgba(7, 17, 27, 0.1);
                font-size: 14px;
            }
            .info-item {
                padding: 0.42rem 0.32rem;
                line-height: 0.42rem;
                border-bottom: 1px solid rgba(7, 17, 27, 0.1);
                font-size: 12px;
                &:last-child {
                    border: none;
                }
            }
        }
    }
</style>



